import Vue from 'vue';
import Router from 'vue-router';
import Home from '@/components/Home';

const Peroni = () => import( '@/components/Peroni.vue' );
const BayerAspirin = () => import( '@/components/BayerAspirin.vue' );
const BayerLasonil = () => import( '@/components/BayerLasonil.vue' );
const Tempo = () => import( '@/components/Tempo.vue' );
const Yamaha = () => import( '@/components/Yamaha.vue' );


Vue.use( Router );

export default new Router( {
	mode: 'history',
	routes: [
		{
			path: '/',
			name: 'Home',
			component: Home
		},
		{
			path: '/peroni',
			name: 'peroni',
			component: Peroni,
		},
		{
			path: '/bayer-aspirin',
			name: 'bayer-aspirin',
			component: BayerAspirin,
		},
		{
			path: '/bayer-lasonil',
			name: 'bayer-lasonil',
			component: BayerLasonil,
		},
		{
			path: '/tempo',
			name: 'tempo',
			component: Tempo,
		},
		{
			path: '/yamaha',
			name: 'yamaha',
			component: Yamaha,
		},
	]
} )



