<template functional>
	<article :class="props.classes">
		<p>
			A senior planner at <a href="http://www.dlvbbdo.com/">DLV BBDO</a> leading the strategy of a number of
			the agency's key accounts, including
			<router-link to="works/che-banca">CheBanca!</router-link>,
			<router-link to="works/bayer">Bayer</router-link>,
			<router-link to="works/philadelphia">Philadelphia</router-link>,
			<router-link to="works/sisal">Sisal</router-link> and
			<router-link to="works/yamaha">Yamaha</router-link>.
			Integrated strategist passionate about the intersection of brand storytelling, people
			and culture.
		</p>
		<p>
			Catch me over <a href="https://www.linkedin.com/pub/alessandro-mese/6/602/bb6/it">linkedin</a> or see a
			selection
			of my
			<router-link to="/works">works</router-link>
		</p>
	</article>
</template>

<script>
	export default {
		name: "MainCopy",
		props: {
			classes: {
				type: String,
				required: false,
			}
		}
	}
</script>

<style scoped>
	p {
		font-size: 1.5vw;
		line-height: 1.7;
	}
	
	article {
		grid-area: content;
	}
</style>



